<template>
  <div class="home-button" @click.stop="toHome">
    <div class="outer"></div>
  </div>
</template>

<script>
export default {
  methods: {
    toHome() {
      this.$router.push({
        path: "/",
      });
    },
  },
};
</script>

<style lang="scss">
.home-button {
  position: fixed;
  top: 24px;
  left: calc(50% - 24px);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 46px;
  height: 46px;
  text-align: center;
  border-radius: 999em;
  border: 1px solid #d6d6d6;
  cursor: pointer;
  background-color: #f5f5f5;
  box-shadow: 0 0 10px 2px #cccccc;
  &:hover {
    background-color: #d6d6d6;
  }
  .outer {
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    border: 3px solid #505050;
  }
}
</style>