<template>
  <div class="commemoration-day">
    
    <home-button />

    <div class="main">
      <p class="text">我们在一起：</p>
      <p class="time">{{ time }}</p>

      <div class="now-day">当前日期： {{ data }}</div>
    </div>
  </div>
</template>

<script>
import HomeButton from "@/components/HomeButton/index";

export default {
  data() {
    return {
      timerId: null,
      week: ["SUN", "MON", "TUE", "WED", "THU", "FRI", "SAT"],
      time: "",
      data: "",
      nowDate: new Date(),
      oldDate: new Date("2021/09/13 03:00"),
    };
  },
   components: {
    HomeButton,
  },
  created() {
    // 由于定时器，第一次执行会有延迟时间，所以此次我们先调用一次，后续灾后定时器的方法
    this.updateTime();
    // 页面初始化,开启定时器，更新时间
    this.timerId = setInterval(this.updateTime, 1000);
  },
  methods: {
    updateTime() {
      // 当前时间
      let cd = new Date();
      // 纪念日
      let total = (cd.getTime() - this.oldDate.getTime()) / 1000;

      // 格式化当前时间
      this.data = this.getNowDate(cd);

      // 获取格式化后的纪念日
      this.time = this.getCommemoration(total);
    },

    //  格式化时间位数
    zeroPadding(num, digit) {
      let zero = "";
      for (let i = 0; i < digit; i++) {
        zero += "0";
      }
      return (zero + num).slice(-digit);
    },

    // 格式化当前时间
    getNowDate(cd) {
      return (
        this.zeroPadding(cd.getFullYear(), 4) +
        "-" +
        this.zeroPadding(cd.getMonth() + 1) +
        "-" +
        this.zeroPadding(cd.getDate(), 2) +
        " " +
        this.week[cd.getDay()]
      );
    },

    // 格式化倒计时
    getCommemoration(total) {
      let day = parseInt(total / (24 * 60 * 60)); //计算整数天数
      let afterDay = total - day * 24 * 60 * 60; //取得算出天数后剩余的秒数
      let hour = parseInt(afterDay / (60 * 60)); //计算整数小时数
      let afterHour = total - day * 24 * 60 * 60 - hour * 60 * 60; //取得算出小时数后剩余的秒数
      let min = parseInt(afterHour / 60); //计算整数分
      let afterMin = parseInt(
        total - day * 24 * 60 * 60 - hour * 60 * 60 - min * 60
      ); //取得算出分后剩余的秒数

      // 纪念日
      return (
        day +
        " 天 " +
        this.zeroPadding(hour, 2) +
        " 小时 " +
        this.zeroPadding(min, 2) +
        " 分钟 " +
        this.zeroPadding(afterMin, 2) +
        " 秒 "
      );
    },
  },

  // 页面销毁，定时器不会清除，需要手动，在销毁钩子函数中清除
  beforeDestroy() {
    clearInterval(this.timerId);
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/variables.scss";
.commemoration-day {
  position: relative;
  width: 100%;
  height: 100%;
  background: #0f3854;
  background: radial-gradient(ellipse at center, #0a2e38 0%, #000000 70%);
  background-size: 100%;

  .main {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: $highlight-text-color;
    text-shadow: 0 0 20px rgba(10, 175, 230, 1), 0 0 20px rgba(10, 175, 230, 0);
    text-align: center;
    .text {
      margin-right: -40px;
      letter-spacing: 0.1em;
      font-size: 24px;
      text-align: left;
    }
    .time {
      font-size: 80px;
      padding: 5px 0;
    }
    .now-day {
      padding-top: 10px;
      letter-spacing: 0.1em;
      font-size: 24px;
      text-align: right;
    }
  }
}
</style>